
body {
  font-family: 'Arial', sans-serif;
  background-color: #f4f4f4;
  color: #333;
  line-height: 1.6;
}

.App {
  width: 80%;
  margin: auto;
  max-width: 1200px;
}

/* Article Component Styles */
.Article {
  background-color: #fff;
  margin: 20px 0;
  padding: 20px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  border-radius: 10px;
}

.Article h2 {
  color: #0077cc;
}

.Article p {
  font-size: 1.1em;
  margin-bottom: 10px;
}

/* Adding some spacing and style to the header */
h1 {
  text-align: center;
  color: #333;
  padding: 20px 0;
  font-size: 2.5em;
}

/* Style for strong (bold) elements */
strong {
  color: #0077cc;
}

.AboutMe {
  background-color: #fff;
  margin: 20px 0;
  padding: 20px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  border-radius: 10px;
  text-align: center; /* Center text horizontally */
  display: flex;
  flex-direction: column;
  justify-content: center; /* Center content vertically */
  align-items: center; /* Ensure all items are centered horizontally in flexbox */
}

.AboutMe h2 {
  color: #0077cc;
}

.AboutMe p {
  font-size: 1.1em;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
